import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: "170px",
    overfloWx: "hidden"
  },
  header: {
    fontFamily: "Futura-Bold",
    fontSize: "20px",
    textAlign: "center",
    fontFamily: "Roboto",
    fontWeight: 500,
    marginBottom: "40px !important",
  },
  firstCon: {
    display: "flex",
    justifyContent: "center",
  },
  secondCon: {
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "column",
    alignItems: "center",
    marginTop: theme.spacing(2),
  },
  card1: {
    minHeight: "50vh",
    marginTop: theme.spacing(5),
    [theme.breakpoints.only("lg")]: {
      width: "50%",
    },
    [theme.breakpoints.only("md")]: {
      width: "50%",
    },
    [theme.breakpoints.only("xs")]: {
      width: "90%",
    },
    [theme.breakpoints.only("sm")]: {
      width: "70%",
    },
  },
  btn1: {
    fontSize: "15px",
    padding: "10px 20px",
    backgroundColor: "#35cf94",
    width: "100px",
    height: "30px",
    color: "white",
    "&:hover": {
      backgroundColor: "#35cf94",
    },
  },
  input: {
    minWidth: "200px",
    maxWidth: "500px",
    fontSize: "30px !important",
    [theme.breakpoints.up("md")]: {
      width: "50%",
    },
    [theme.breakpoints.only("sm")]: {
      width: "80%",
    },
    [theme.breakpoints.only("sm")]: {
      width: "80%",
    },
    "& .MuiInput-underline": {},
  },
  btnbtn: {
    marginTop: "50px",
    marginBottom: "50px",
  },
  form: {
    width: "100%",
    minWidth: "200px",
    maxWidth: "400px",
  },
}));
