import { Button, Grid, Typography } from '@material-ui/core'
import { api } from 'api'
import { BonfireField } from 'components/BonfireField/BonfireField'
import Footer from 'components/Footer'
import Header from 'components/Header'
import { useNotifications } from 'context/NotificationsContext'
import { Form, Formik } from 'formik'
import React from 'react'
import { useStyles } from './ForgotPassword.style'

const ForgotPassword = () => {
    const classes = useStyles();
    const { notify } = useNotifications();
    const initialValues = {
        email: ""
    }
    const recoverPassword = async (data): Promise<void> => {
        try {
            const axios = await api();
            await axios.post(`user/forgotPassword`, data)
            notify({
                type: "success",
                message: "Şifrə yeniləmə linki mailinizə göndərildi!"
            })
        } catch (error) {
            notify({
                type: "error",
                message: "Yenidən cəhd edin!"
            })
        }
    }
    return (
        <>
            <Header />
            <Grid container className={classes.container}>
                <Grid item xs={12}>
                    <Typography className={classes.header}>
                        Zəhmət olmasa e-mailinizi qeyd edin.
                    </Typography>
                </Grid>

                <Grid item xs={12}>
                    <Formik
                        initialValues={initialValues}
                        onSubmit={recoverPassword}
                        enableReinitialize
                    >
                        {({ submitForm }) => {
                            return (
                                <Form>
                                    <Grid className={classes.firstCon} container lg={12}>
                                        <Grid container className={classes.secondCon} item spacing={3}>
                                            <div className={classes.form}>
                                                <BonfireField
                                                    name="email"
                                                    type="email"
                                                    placeholder="E-mail"
                                                />
                                            </div>
                                            <Grid className={classes.btnbtn} item>
                                                <Button onClick={submitForm} variant="contained" className={`${classes.btn1} btn-primary btn-sm`}>
                                                    Göndər
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Form>
                            );
                        }}
                    </Formik>
                </Grid>
            </Grid>
            <Footer />
        </>
    )
}
export default ForgotPassword;